* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  background-color: #f0f2f5;
  height: 100vh;
  display: flex;
  overflow: hidden;
}

.container {
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  width: 100%;
  min-width: 680px;
  display: flex;
  overflow: hidden;
  box-shadow: 0 4px 10px #0000001a;
}

.alchemy-section {
  background-color: #f7f7f7;
  border-bottom: 1px solid #ddd;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  display: flex;
}

.alchemy-wrapper {
  gap: 10px;
  display: flex;
}

.alchemy-section label {
  margin-right: 10px;
  font-weight: bold;
}

#alchemy-key {
  border: 1px solid #ddd;
  border-radius: 8px;
  flex-grow: 1;
  padding: 10px;
  font-size: 1rem;
}

#init-btn {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  font-size: 1rem;
  transition: background-color .2s;
}

#init-btn:hover {
  background-color: #0056b3;
}

.chat-log {
  background-color: #fafafa;
  flex-direction: column-reverse;
  flex: 1;
  padding: 20px;
  display: flex;
  overflow-y: auto;
}

.chat-log .message-pair {
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 15px;
  display: flex;
}

.chat-log .message {
  background-color: #fff;
  border-left: 4px solid #007bff;
  border-radius: 8px;
  flex: 1;
  align-items: flex-start;
  padding: 15px;
  transition: background .2s;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.chat-log .message:hover {
  background-color: #f0f2f7;
}

.chat-log .message .content {
  white-space: pre-wrap;
  word-break: break-word;
  flex-grow: 1;
  font-family: monospace;
}

.chat-log .message .id {
  color: #888;
  margin-left: 15px;
  font-size: .9rem;
}

.json-key {
  color: #a71d5d;
}

.json-value {
  color: #0086b3;
}

.json-string {
  color: #63a35c;
}

.input-section {
  background-color: #fff;
  border-top: 1px solid #ddd;
  flex-direction: column;
  padding: 15px;
  display: flex;
}

.input-wrapper {
  flex-grow: 1;
}

#json-input {
  resize: none;
  white-space: pre;
  overflow-wrap: break-word;
  background-color: #f0f2f5;
  border: 1px solid #ddd;
  border-radius: 8px;
  outline: none;
  width: 100%;
  padding: 15px;
  font-family: monospace;
  font-size: 1rem;
  line-height: 1.5;
}

.send-container {
  align-items: center;
  gap: 10px;
  margin-top: 10px;
  display: flex;
}

#send-btn {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 8px;
  padding: 10px 20px;
  font-size: 1rem;
  transition: background-color .2s;
}

#send-btn:hover {
  background-color: #0056b3;
}

.status-btn {
  cursor: pointer;
  background-color: #f0f2f5;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 10px 15px;
  transition: background-color .2s;
}

.template-buttons {
  gap: 10px;
  display: flex;
}

.template-btn {
  cursor: pointer;
  background-color: #f0f2f5;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 10px 15px;
  transition: background-color .2s;
}

.template-btn:hover {
  background-color: #e0e0e0;
}

.modal-overlay {
  background: #000000b3;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-content {
  background-color: #fff;
  border-radius: 8px;
  width: 40%;
  min-width: 300px;
  max-width: fit-content;
  padding: 40px;
  position: relative;
}

.modal-content h2 {
  margin-bottom: 20px;
}

.modal-content li {
  margin-bottom: 5px;
}

@media only screen and (width <= 768px) {
  .chat-log .message-pair {
    flex-direction: column;
  }

  #json-input, #send-btn {
    font-size: .9rem;
  }

  .chat-log .message {
    padding: 10px;
  }
}
